import {
Runtime,
Inspector
} from "@observablehq/runtime";
import { Compiler } from "@alex.garcia/unofficial-observablehq-compiler";
const compile = new Compiler();
compile.module(
`
md\` This cell contains **markdown**\`
`
)
.then(define => {
const rt = new Runtime();
window.MODULE = rt.module(
define,
Inspector.into(document.querySelector("#main"))
);
});
